html {
  height: 100%; }

.page {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: normal;
  min-height: 100%;
  color: #2C2E3F; }

.navigation {
  background-color: #ffffff; }

.navigation__close,
.navigation__open,
.navigation__toggle {
  display: none; }

.navigation__destinations {
  list-style: none; }

.navigation__inner {
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  justify-content: space-between;
  width: 1024px;
  padding-top: 36px;
  padding-bottom: 31px; }

.navigation__destinations {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0; }

.navigation__item:not(:last-child) {
  padding-right: 40px; }

.navigation__logo {
  width: 55px;
  height: 22px; }

.navigation__link-tel::before {
  content: "";
  position: absolute;
  width: 25px;
  height: 30px;
  background-image: url(../img/phone-icon.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
  right: 170px;
  top: 0px; }

.navigation__link {
  font-size: 14px;
  line-height: 17px; }

.navigation__link-tel {
  font-size: 18px;
  line-height: 32px;
  text-decoration: underline;
  position: relative; }

.navigation__link:hover,
.navigation__link:focus {
  color: #FE7865; }

.catalog__inner {
  background-image: url(../img/background-catalog@1x.png);
  background-size: 1444px;
  background-position: 0 0;
  background-repeat: no-repeat;
  min-width: 1024px;
  min-height: 800px; }

.catalog__image {
  position: relative;
  max-width: 1920px;
  margin: 0 auto; }

.catalog__image-panorama {
  min-height: 860px;
  width: 100%;
  padding: 0;
  max-width: 1920px;
  margin: 0 auto; }

.footer__inner {
  padding: 10px 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }

.footer__destinations-inner {
  display: flex; }

.destinations__list {
  list-style: none;
  padding: 0; }
